import { makeAutoObservable } from 'mobx';
import { container, Lifecycle } from 'tsyringe';
import { AuthService } from '../service/auth.service';
import { UIStore } from '../store/ui.store';
import { InjectionProvider } from './types';

export function injectionToken(type: string) {
    return Symbol(type);
}

export const STORE_PROVIDERS = [
    {token: UIStore, useClass: UIStore, options: {lifecycle: Lifecycle.Singleton}},
    {token: AuthService, useClass: AuthService, options: {lifecycle: Lifecycle.Singleton}}
];

export function storeProviders(providers: InjectionProvider[]) {
    providers.forEach(({token, options, ...provider}) => {
        container.register(token, provider as any, options);

        container.afterResolution(
            token,
            (_, res) => {
                makeAutoObservable(res);
            },
            {frequency: 'Once'}
        )
    })
}
