import * as React from 'react';
import { render } from 'react-dom';
import { container } from 'tsyringe';
import { App, AppProps } from './application';
import { injectionToken, STORE_PROVIDERS, storeProviders } from './di/ioc';
import './i18n'
import { EXAMPLE_TOKEN } from './example_tokens';
import { Routes } from './routes';
import './styles.scss'

type ApplicationConfig = {
    rootNode: HTMLElement;
}


const APP_ROUTES = injectionToken('Application Root Routes');
const APP_PROVIDER = injectionToken('Application');

container.register(APP_ROUTES, {useValue: Routes})
container.register(APP_PROVIDER, {useValue: App})

export const main = ({rootNode, ...options}: ApplicationConfig) => {
    const Application = container.resolve<React.FC<AppProps>>(APP_PROVIDER);
    const Routes = container.resolve<React.FC>(APP_ROUTES);

    render(
        <Application
            renderRoutes={() => <Routes/>}
            {...options}
        />
        ,
        rootNode
    );
}

(function bootstrap() {
    storeProviders(STORE_PROVIDERS);
    container.register(EXAMPLE_TOKEN, {useValue: 'SOME VALUE'})

    main({
        rootNode: document.getElementById('root')
    });
})();

declare const module;
if (module.hot) {
    module.hot.accept();
}
