import * as React from 'react';
import { Container } from '../components/layout/container';
import { Header } from '../components/layout/header';

const MENU_MOCK = [
    {title: 'Home'},
    {title: 'Price'}
]

export const NotAuthorized: React.FC = ({children}) => {

    return (
        <>
            <Header links={MENU_MOCK}/>

            <Container>
                {children}
            </Container>
        </>
    )
}

