import * as React from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom'
import { Button } from '../../ui/button/button';
import styles from './header.scss';


export type HeaderProps = {
    links: Array<{
        title: string,
    }>
}

export const Header: React.FC<HeaderProps> = ({
    links = []
}) => {
    const {push} = useHistory()
    const {t} = useTranslation()

    const menu = (
        links.length > 0 && (
            <ul className={styles.headerMenu}>
                {links.map(({title}, index) => (
                    <li key={title + index}
                        className={styles.headerMenu__link}>
                        {title}
                    </li>
                ))}
            </ul>
        )
    )

    return (
        <div className={styles.header}>
            <div className={styles.header__content}>
                <span className={styles.header__logo}/>


                <div className={styles.header__menuContainer}>
                    {menu}

                    <div className={styles.header__profileMenu}>
                        <Button
                            onClick={() => push('/auth/login')}>{t('Login')}</Button>

                        <Button
                            onClick={() => push('/auth/signup')}
                            style={{marginLeft: '10px'}} rounded>{t('Register')}</Button>
                    </div>
                </div>
            </div>
        </div>
    )
}
