import { useFormik } from 'formik';
import * as React from 'react';
import { useTranslation } from 'react-i18next';
import { useDi } from '../../di/hooks';
import { di } from '../../di/inject';
import { EXAMPLE_TOKEN } from '../../example_tokens';
import { AuthService } from '../../service/auth.service';
import { Button } from '../../ui/button/button';
import { Field } from '../../ui/form/field';
import { Input } from '../../ui/input/input';

import styles from './logIn.scss'

export const LogIn: React.FC<{}> = di(
    function LoginForm({}) {
        const {t} = useTranslation()

        const auth = useDi(AuthService)
        const token = useDi(EXAMPLE_TOKEN)

        const signInForm = useFormik({
            initialValues: {
                email: '',
                password: ''
            },
            onSubmit: values => auth.login(values)
        });

        return (
            <div className={styles.signIn}>
                <h1 className={styles.signIn__title}>{t('LoginTitle')}</h1>
                <p className={styles.signIn__desc}>{t('LoginDesc')}</p>

                {token}

                <form onSubmit={signInForm.handleSubmit} className={styles.signIn__form}>
                    <Field
                        label={t('Email')}
                        renderInput={() =>
                            <Input
                                name="email"
                                placeholder={t('Email')}
                                onChange={signInForm.handleChange}/>
                        }
                    />

                    <Field
                        label={t('Password')}
                        renderInput={() =>
                            <Input
                                type='password'
                                name="password"
                                placeholder={t('Password')}
                                onChange={signInForm.handleChange}/>
                        }/>

                    <Button
                        type="submit"
                        large
                        fluid>{t('Login')}</Button>
                </form>
            </div>
        )
    },

    {
        token: EXAMPLE_TOKEN,
        useValue: 'String'
    }
)
